.project-create-container .project-create-form {
  background: #FFFFFF;
  box-shadow: 0px 5px 25px rgba(61, 74, 86, 0.15);
  border-radius: 7px;
  margin: 40px auto 0;
  width: 440px;
  padding: 30px;
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.project-create-container .project-create-form > .header {
  font-size: 22px;
  line-height: 30px;
  color: #3D4A56;
}
.project-create-container .project-create-form .site-input {
  border: 2px solid #DEE3E9;
  border-radius: 7px;
  outline: none;
  width: calc(100% - 30px);
  height: 23px;
  font-weight: 400;
  font-size: 17px;
  line-height: 23px;
  color: #3D4A56;
  padding: 10px 15px;
}
.project-create-container .project-create-form .site-input:focus,
.project-create-container .project-create-form .site-input:hover {
  border: 2px solid #1975FF;
}
.project-create-container .project-create-form .label {
  font-weight: 600;
  font-size: 17px;
  line-height: 23px;
  color: #3D4A56;
}
.project-create-container .project-create-form .searching-engine {
  display: flex;
  flex-direction: row;
  gap: 10px;
}
.project-create-container .project-create-form .searching-engine .label {
  width: 200px;
}
.project-create-container .project-create-form .searching-engine .checkboxes {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.project-create-container .project-create-form .searching-engine .checkboxes .checkbox {
  width: 16px;
  height: 16px;
  border: 2px solid #DEE3E9;
  border-radius: 1px;
  margin-right: 7px;
}
.project-create-container .project-create-form .searching-engine .checkboxes .checkbox:hover {
  cursor: pointer;
}
.project-create-container .project-create-form .searching-engine .checkboxes .checkbox + span {
  font-weight: 400;
  font-size: 17px;
  line-height: 23px;
  color: #3D4A56;
}
.project-create-container .project-create-form .searching-engine .checkboxes .checkbox + span:hover {
  cursor: pointer;
}
.project-create-container .project-create-form .searching-depth {
  display: flex;
  flex-direction: row;
  gap: 10px;
}
.project-create-container .project-create-form .searching-depth .label {
  width: 200px;
}
.project-create-container .project-create-form .searching-depth .checkboxes {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.project-create-container .project-create-form .searching-depth .checkboxes .checkbox {
  display: none;
}
.project-create-container .project-create-form .searching-depth .checkboxes .checkbox + span:before {
  display: block;
  content: ' ';
  width: 16px;
  height: 16px;
  border: 2px solid #DEE3E9;
  border-radius: 50%;
  margin: auto 7px auto 0;
}
.project-create-container .project-create-form .searching-depth .checkboxes .checkbox + span:before:hover {
  cursor: pointer;
}
.project-create-container .project-create-form .searching-depth .checkboxes .checkbox:checked + span:before {
  width: 4px;
  height: 4px;
  border: 8px solid #1975FF;
}
.project-create-container .project-create-form .searching-depth .checkboxes .checkbox + span {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 200px;
  font-weight: 400;
  font-size: 17px;
  line-height: 23px;
  color: #3D4A56;
}
.project-create-container .project-create-form .searching-depth .checkboxes .checkbox + span > span {
  width: 85%;
  text-align: left;
}
.project-create-container .project-create-form .searching-depth .checkboxes .checkbox + span:hover {
  cursor: pointer;
}
.project-create-container .project-create-form .cities-container .city-input {
  border: 2px solid #DEE3E9;
  border-radius: 7px;
  outline: none;
  width: calc(100% - 30px);
  height: 23px;
  font-weight: 400;
  font-size: 17px;
  line-height: 23px;
  color: #3D4A56;
  padding: 10px 15px;
  margin-bottom: 15px;
}
.project-create-container .project-create-form .cities-container .city-input:focus,
.project-create-container .project-create-form .cities-container .city-input:hover {
  border: 2px solid #1975FF;
}
.project-create-container .project-create-form .cities-container .cities {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.project-create-container .project-create-form .cities-container .city {
  background: #E7F0F8;
  border: 2px solid #E7F0F8;
  width: calc(100% - 30px);
  height: 23px;
  border-radius: 7px;
  padding: 10px 15px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.project-create-container .project-create-form .cities-container .city .name {
  font-weight: 400;
  font-size: 17px;
  line-height: 23px;
  color: #3D4A56;
}
.project-create-container .project-create-form .cities-container .city .delete {
  color: #1975FF;
  font-size: 32px;
  line-height: 16px;
  height: 16px;
  margin: auto 0;
}
.project-create-container .project-create-form .cities-container .city .delete:hover {
  cursor: pointer;
}
.project-create-container .project-create-form .time .time-container {
  display: flex;
  margin-top: 15px;
}
.project-create-container .project-create-form .time .time-container input::-webkit-outer-spin-button,
.project-create-container .project-create-form .time .time-container input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.project-create-container .project-create-form .time .time-container input {
  -moz-appearance: textfield;
  background: #FFFFFF;
  border: 2px solid #DEE3E9;
  border-radius: 7px;
  width: 51px;
  height: 36px;
  font-size: 17px;
  line-height: 17px;
  text-align: center;
  color: #3D4A56;
  padding: 0;
  outline: none;
}
.project-create-container .project-create-form .time .time-container span {
  display: inline-block;
  width: 20px;
  margin: auto 0;
  justify-content: start;
  font-size: 17px;
  line-height: 17px;
  text-align: center;
  color: #3D4A56;
}
.project-create-container .project-create-form .days-container .header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.project-create-container .project-create-form .days-container .header .off {
  font-size: 17px;
  line-height: 23px;
  text-align: right;
  color: #A5AFBB;
}
.project-create-container .project-create-form .days-container .header .off:hover {
  cursor: not-allowed;
}
.project-create-container .project-create-form .days-container .header .off.can {
  color: #3D4A56;
}
.project-create-container .project-create-form .days-container .header .off.can:hover {
  cursor: pointer;
}
.project-create-container .project-create-form .days-container .days {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 17px;
  margin: 15px auto 0;
}
.project-create-container .project-create-form .days-container .days .day {
  font-size: 17px;
  line-height: 40px;
  text-align: center;
  color: #616872;
  width: 40px;
  height: 40px;
  background: #F4F8FB;
  border-radius: 7px;
}
.project-create-container .project-create-form .days-container .days .day:hover {
  cursor: pointer;
}
.project-create-container .project-create-form .days-container .days .day.selected {
  background: #1975FF;
  color: #fff;
}
.project-create-container .project-create-form .button-container .button.add-site {
  background: #1975FF;
  border-radius: 7px;
  width: 100%;
  height: 50px;
  outline: none;
  border: none;
  font-size: 16px;
  line-height: 22px;
  color: #FFFFFF;
}
.project-create-container .project-create-form .button-container .button.add-site:hover {
  cursor: pointer;
}
.search-popup {
  width: 410px;
}
.search-popup .city:hover {
  cursor: pointer;
}
