.header-container {
  background: #F4F8FB;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 83px;
}
.header-container .header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 1180px;
  margin: auto 0;
}
.header-container .header .heading {
  display: flex;
  flex-direction: row;
}
.header-container .header .heading .logo {
  background: url(../svg/logo.svg);
  width: 53px;
  height: 45px;
}
.header-container .header .heading div {
  display: flex;
  flex-direction: column;
  margin-left: 9px;
}
.header-container .header .heading div span:nth-of-type(1) {
  color: #1975FF;
  font-size: 24px;
  font-weight: 600;
  line-height: 33px;
}
.header-container .header .heading div span:nth-of-type(2) {
  color: #3D4A56;
  font-size: 14px;
  font-weight: 400;
  line-height: 10px;
}
.header-container .header .middle {
  margin: auto 0;
}
.header-container .header .middle .links {
  display: flex;
  flex-direction: row;
  gap: 30px;
  margin: auto 0;
}
.header-container .header .middle .links a {
  position: relative;
  font-weight: 400;
  font-size: 17px;
  line-height: 23px;
  color: #34495E;
  text-decoration: none;
}
.header-container .header .middle .links a:hover::before {
  position: absolute;
  content: ' ';
  width: 36px;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  border-bottom: 2px solid #1975FF;
}
.header-container .header .unauthorized {
  margin: auto 0;
  gap: 30px;
}
.header-container .header .unauthorized .item {
  margin: auto 0;
  display: flex;
  flex-direction: row;
  gap: 7px;
}
.header-container .header .unauthorized .item .icon {
  width: 25px;
  height: 25px;
  margin: auto 0;
}
.header-container .header .unauthorized .item .label {
  font-weight: 500;
  font-size: 17px;
  line-height: 23px;
  color: #34495E;
  position: relative;
}
.header-container .header .unauthorized .item:hover {
  cursor: pointer;
}
.header-container .header .unauthorized .item:hover .label::before {
  position: absolute;
  content: ' ';
  width: 36px;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  border-bottom: 2px solid #1975FF;
}
.header-container .header .unauthorized .login .icon {
  background: url(../svg/login-icon.svg) no-repeat;
}
.header-container .header .unauthorized .register .icon {
  background: url(../svg/register-icon.svg) no-repeat;
}
.header-container .header .right {
  display: flex;
  flex-direction: row;
}
.header-container .header .right .projects {
  display: flex;
  flex-direction: row;
}
.header-container .header .right .projects .open-projects {
  background: #FFFFFF;
  border: 0 solid transparent;
  border-radius: 7px;
  width: 43px;
  height: 43px;
  position: relative;
}
.header-container .header .right .projects .open-projects .backpack {
  background: url(../svg/backpack.svg) no-repeat;
  width: 25px;
  height: 25px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.header-container .header .right .projects .open-projects:hover {
  background: #1975FF;
  cursor: pointer;
}
.header-container .header .right .projects .open-projects:hover .backpack {
  background-image: url(../svg/backpack-white.svg);
}
.header-container .header .right .projects .search-input-container {
  background: #FFFFFF;
  border: 0 solid transparent;
  border-radius: 7px;
  padding: 0;
  height: 43px;
  margin-left: 5px;
  width: 320px;
  display: flex;
  flex-direction: row;
}
.header-container .header .right .projects .search-input-container .search-icon {
  display: block;
  margin: auto 10px;
  background: url(../svg/search-icon.svg) no-repeat;
  width: 25px;
  height: 25px;
}
.header-container .header .right .projects .search-input-container .search-input {
  border: none;
  outline: none;
  font-weight: 400;
  font-size: 17px;
  line-height: 23px;
  color: #34495E;
  margin: auto 0;
  width: 270px;
}
.header-container .header .right .messages {
  width: 25px;
  height: 25px;
  margin: auto 31px auto 39px;
  background: url(../svg/messages.svg) no-repeat;
}
.header-container .header .right .messages:hover {
  cursor: pointer;
}
.header-container .header .right .help {
  width: 25px;
  height: 25px;
  margin: auto 31px auto 0;
  background: url(../svg/help.svg) no-repeat;
}
.header-container .header .right .help:hover {
  cursor: pointer;
}
.header-container .header .right .balance-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 43px;
  width: 170px;
  background: #FFFFFF;
  border: 0 solid transparent;
  border-radius: 7px;
}
.header-container .header .right .balance-container .left {
  display: flex;
  flex-direction: row;
  gap: 7px;
  margin: auto 0 auto 11px;
}
.header-container .header .right .balance-container .left .wallet {
  width: 25px;
  height: 25px;
  background: url(../svg/wallet.svg) no-repeat;
}
.header-container .header .right .balance-container .left .balance {
  font-weight: 400;
  font-size: 17px;
  line-height: 23px;
  color: #34495E;
}
.header-container .header .right .balance-container .plus {
  margin: auto 10px auto 0;
  width: 22px;
  height: 22px;
  background: url(../svg/plus.svg) no-repeat;
}
.header-container .header .right .balance-container .plus:hover {
  cursor: pointer;
}
.header-container .header .right .cabinet-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 43px;
  width: 67px;
  background: #FFFFFF;
  border: 0 solid transparent;
  border-radius: 7px;
  margin-left: 20px;
}
.header-container .header .right .cabinet-container .avatar {
  width: 25px;
  height: 25px;
  background: url(../svg/avatar.svg) no-repeat;
  margin: auto 0 auto 10px;
}
.header-container .header .right .cabinet-container .marker {
  margin: auto 10px auto 0;
  width: 22px;
  height: 22px;
  background: url(../svg/arrow-down.svg) no-repeat;
}
.header-container .header .right .cabinet-container .marker:hover {
  cursor: pointer;
}
.header-container .header .mobile-open-menu {
  display: none;
}
.search-popup {
  z-index: 10;
  width: 290px;
  height: auto;
  border-radius: 7px;
  background: #F4F8FB;
  box-shadow: 0px 1px 6px rgba(61, 74, 86, 0.15), 0px 2px 15px rgba(61, 74, 86, 0.1);
  display: flex;
  flex-direction: column;
  padding: 20px 10px 20px 20px;
  gap: 17px;
}
.search-popup .search-result {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 22px;
}
.search-popup .search-result .name {
  font-weight: 400;
  font-size: 17px;
  line-height: 17px;
  color: #3D4A56;
  margin: auto 0;
}
.search-popup .search-result svg {
  display: none;
}
.search-popup .search-result:hover .name {
  color: #1975FF;
}
.search-popup .search-result:hover .name:hover {
  cursor: pointer;
}
.search-popup .search-result:hover svg {
  display: unset;
}
.search-popup .search-result:hover svg:hover {
  cursor: pointer;
}
