@import "variables";

body, html {
  font-family: @main-font;
  font-weight: 600;
  font-style: normal;
  padding: 0;
  margin: 0;
}

html, body, #root, .app {
  height: 100%;
  width: 100%;
  min-width: 1200px;
}

html {
  scrollbar-width: none;
}
html::-webkit-scrollbar {
  display: none;
}

input, textarea {
  &:focus::placeholder {
    color: transparent !important;
  }
}

button:not([disabled]) {
  cursor: pointer;
}

@media screen and (max-width: 479px) {
  @import './main.mobile.less';
  @import './main-page.mobile.less';
  @import './footer.mobile.less';
  @import './header.mobile.less';
}