}
.player-modal {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 100;
}
.player-modal:not(.opened) {
  display: none;
}
.player-modal .player {
  position: absolute;
  box-shadow: 0 6px 30px rgba(46, 57, 67, 0.1);
  border-radius: 20px;
  padding: 10px;
  background: #FFF;
  z-index: 10;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.player-modal .player iframe {
  width: 980px;
  height: 470px;
  border-radius: 20px;
  margin: auto;
}
.player-modal .close {
  position: absolute;
  font-size: 32px;
  line-height: 32px;
  color: #FFF;
  opacity: 0.8;
  top: 10px;
  right: 20px;
  z-index: 11;
}
.player-modal .close:hover {
  opacity: 1;
  cursor: pointer;
}
.player-modal .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(61, 74, 86, 0.75);
  z-index: 1;
}
