.sidebar {
  display: flex;
  flex-direction: column;
  border-right: 1px solid #E7F0F8;
  gap: 10px;
  padding-top: 25px;
  padding-right: 10px;
  height: 100%;
}
.sidebar .icon {
  padding: 6px;
}
.sidebar .icon,
.sidebar .icon svg {
  width: 20px;
  height: 20px;
}
.sidebar .active,
.sidebar .icon:hover {
  background-color: #1975FF;
  border-radius: 7px;
}
.sidebar .active svg,
.sidebar .icon:hover svg {
  fill: #1975FF;
}
.sidebar .active svg path,
.sidebar .icon:hover svg path {
  stroke: #fff;
}
.sidebar .line {
  border-bottom: 1px solid #E7F0F8;
  width: calc(100% + 10px);
}
