.no-projects {
  display: flex;
  flex-direction: column;
  margin-top: 120px;
}
.no-projects > * {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0 auto;
}
.no-projects .heading {
  font-size: 22px;
  line-height: 30px;
  color: #3D4A56;
  text-align: center;
  margin-bottom: 15px;
}
.no-projects .text {
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
  color: #3D4A56;
  text-align: center;
}
.no-projects .text:nth-of-type(3) {
  width: 460px;
}
.no-projects .button.add-site {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 10px;
  margin-top: 30px;
  margin-bottom: 50px;
  border: none;
  background: #1975FF;
  border-radius: 7px;
  padding: 15px 25px;
  font-size: 16px;
  line-height: 120%;
  text-align: center;
  color: #FFFFFF;
}
.no-projects .button.add-site:hover {
  cursor: pointer;
}
.no-projects .button.add-site svg {
  margin-top: -3px;
}
.no-projects .button.download-program {
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 7px;
  border: none;
  background: #fff;
  font-size: 16px;
  line-height: 120%;
  text-align: center;
  color: #1975FF;
}
.no-projects .button.download-program:hover {
  cursor: pointer;
}
.no-projects .button.download-program svg {
  margin: -3px 0 auto;
}
.projects-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
.projects-container .projects {
  margin: 0 auto;
  flex-grow: 1;
  width: 1180px;
  background: #FFFFFF;
  box-shadow: 0px 4px 7px rgba(61, 74, 86, 0.1);
}
.projects-container .projects .projects-table .header {
  display: flex;
  flex-direction: row;
  height: 73px;
  padding: 0 25px 0 20px;
  gap: 20px;
  border-bottom: 2px solid #E7F0F8;
}
.projects-container .projects .projects-table .header .filter.active svg path,
.projects-container .projects .projects-table .header .filter:hover svg path {
  stroke: #1975FF;
}
.projects-container .projects .projects-table .header > div {
  margin: auto 0;
}
.projects-container .projects .projects-table .header > div.filter {
  padding-top: 5px;
}
.projects-container .projects .projects-table .header .filter:hover {
  cursor: pointer;
}
.projects-container .projects .projects-table .header div:not(.filter) {
  font-size: 17px;
  line-height: 23px;
  color: #3D4A56;
}
.projects-container .projects .projects-table .header div:not(.filter):not(.name) {
  text-align: center;
}
.projects-container .projects .projects-table .header .name {
  width: 305px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.projects-container .projects .projects-table .header .name .search-icon {
  display: none;
}
.projects-container .projects .projects-table .header .name:hover {
  cursor: pointer;
}
.projects-container .projects .projects-table .header .name:hover .search-icon {
  display: block;
  margin: auto 10px;
  width: 22px;
  height: 22px;
}
.projects-container .projects .projects-table .header .name:hover .search-icon:hover svg path {
  stroke: #1975FF;
}
.projects-container .projects .projects-table .header .search-container {
  width: 305px;
}
.projects-container .projects .projects-table .header .search-container .search-input-container {
  background: #F4F8FB;
  border: 2px solid #E7F0F8;
  border-radius: 7px;
  padding: 0;
  height: 40px;
  display: flex;
  flex-direction: row;
}
.projects-container .projects .projects-table .header .search-container .search-input-container .search-icon {
  display: block;
  margin: auto 10px;
  background: url(../svg/search-icon.svg) no-repeat;
  width: 22px;
  height: 22px;
  background-size: 22px;
}
.projects-container .projects .projects-table .header .search-container .search-input-container .search-input {
  border: none;
  outline: none;
  font-weight: 400;
  font-size: 17px;
  line-height: 23px;
  color: #34495E;
  margin: auto 0;
  background: transparent;
  flex-grow: 1;
  padding-right: 10px;
  padding-left: 10px;
}
.projects-container .projects .projects-table .header .collection-date {
  width: 105px;
}
.projects-container .projects .projects-table .header .queries-count {
  width: 95px;
}
.projects-container .projects .projects-table .header .top-10,
.projects-container .projects .projects-table .header .top-20,
.projects-container .projects .projects-table .header .top-30,
.projects-container .projects .projects-table .header .top-50,
.projects-container .projects .projects-table .header .top-100 {
  width: 95px;
}
.projects-container .projects .projects-table .projects-body {
  display: flex;
  flex-direction: column;
  position: relative;
}
.projects-container .projects .projects-table .projects-body .project {
  display: flex;
  flex-direction: row;
  padding: 15px 25px 15px 20px;
  border-bottom: 1px solid #E7F0F8;
  gap: 20px;
  height: 24px;
}
.projects-container .projects .projects-table .projects-body .project > div {
  margin: auto 0;
}
.projects-container .projects .projects-table .projects-body .project .marker-container {
  width: 22px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.projects-container .projects .projects-table .projects-body .project .marker-container:hover {
  cursor: pointer;
}
.projects-container .projects .projects-table .projects-body .project .marker {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin: auto;
}
.projects-container .projects .projects-table .projects-body .project .marker.green {
  background: #73C571;
}
.projects-container .projects .projects-table .projects-body .project .marker.pink {
  background: #E7466D;
}
.projects-container .projects .projects-table .projects-body .project .marker.light-blue {
  background: #7BA9EE;
}
.projects-container .projects .projects-table .projects-body .project .marker.peach {
  background: #EEA37B;
}
.projects-container .projects .projects-table .projects-body .project .marker.violet {
  background: #867BEE;
}
.projects-container .projects .projects-table .projects-body .project .marker.sand {
  background: #EEDD7B;
}
.projects-container .projects .projects-table .projects-body .project .marker.dark-pink {
  background: #C571B1;
}
.projects-container .projects .projects-table .projects-body .project .marker.brown {
  background: #6F4437;
}
.projects-container .projects .projects-table .projects-body .project .marker.sea {
  background: #46E7C0;
}
.projects-container .projects .projects-table .projects-body .project .marker.light-green {
  background: #46E747;
}
.projects-container .projects .projects-table .projects-body .project .marker.blue {
  background: #1975FF;
}
.projects-container .projects .projects-table .projects-body .project .marker.orange {
  background: #E77046;
}
.projects-container .projects .projects-table .projects-body .project .marker.dark-blue {
  background: #001637;
}
.projects-container .projects .projects-table .projects-body .project .marker.purple {
  background: #7046E7;
}
.projects-container .projects .projects-table .projects-body .project .marker.red {
  background: #F1313D;
}
.projects-container .projects .projects-table .projects-body .project .marker.stopped {
  border-radius: unset;
  background: url(../svg/stopped.svg) no-repeat;
}
.projects-container .projects .projects-table .projects-body .project .marker:hover {
  cursor: pointer;
}
.projects-container .projects .projects-table .projects-body .project .name {
  width: 305px;
  display: flex;
  flex-direction: row;
  margin-left: -5px;
  justify-content: space-between;
}
.projects-container .projects .projects-table .projects-body .project .name .right {
  display: none;
}
.projects-container .projects .projects-table .projects-body .project .name .site-name {
  font-weight: 400;
  font-size: 17px;
  line-height: 23px;
  color: #34495E;
  margin: auto 0;
}
.projects-container .projects .projects-table .projects-body .project .name .site-name:hover {
  font-weight: 600;
  color: #1975FF;
  cursor: pointer;
}
.projects-container .projects .projects-table .projects-body .project .name:hover .right {
  display: flex;
  flex-direction: row;
  gap: 8px;
}
.projects-container .projects .projects-table .projects-body .project .name:hover .right svg:hover,
.projects-container .projects .projects-table .projects-body .project .name:hover .right svg.active {
  cursor: pointer;
}
.projects-container .projects .projects-table .projects-body .project .name:hover .right svg:hover path,
.projects-container .projects .projects-table .projects-body .project .name:hover .right svg.active path {
  stroke: #1975FF;
}
.projects-container .projects .projects-table .projects-body .project .collection-date {
  width: 105px;
}
.projects-container .projects .projects-table .projects-body .project .collection-date .date-time {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  color: #3D4A56;
}
.projects-container .projects .projects-table .projects-body .project .collection-date .date-time .date {
  font-weight: 600;
}
.projects-container .projects .projects-table .projects-body .project .collection-date:not(.not-collected) .restart.button {
  display: none;
}
.projects-container .projects .projects-table .projects-body .project .collection-date.not-collected .date-time {
  display: none;
}
.projects-container .projects .projects-table .projects-body .project .collection-date:hover .date-time {
  display: none;
}
.projects-container .projects .projects-table .projects-body .project .collection-date:hover .restart.button,
.projects-container .projects .projects-table .projects-body .project .collection-date.not-collected .restart.button {
  display: flex;
  flex-direction: row;
  gap: 7px;
  background: transparent;
  border: none;
  font-size: 16px;
  line-height: 22px;
  color: #616872;
  margin-left: auto;
  margin-right: auto;
}
.projects-container .projects .projects-table .projects-body .project .collection-date:hover .restart.button:not([disabled]):hover,
.projects-container .projects .projects-table .projects-body .project .collection-date.not-collected .restart.button:not([disabled]):hover {
  cursor: pointer;
  font-weight: 600;
  color: #1975FF;
}
.projects-container .projects .projects-table .projects-body .project .collection-date:hover .restart.button:not([disabled]):hover svg path,
.projects-container .projects .projects-table .projects-body .project .collection-date.not-collected .restart.button:not([disabled]):hover svg path {
  stroke: #1975FF;
}
.projects-container .projects .projects-table .projects-body .project .collection-date:hover .restart.button[disabled]:hover,
.projects-container .projects .projects-table .projects-body .project .collection-date.not-collected .restart.button[disabled]:hover {
  cursor: not-allowed;
}
.projects-container .projects .projects-table .projects-body .project .queries-count {
  font-weight: 400;
  width: 95px;
  height: 35px;
  font-size: 16px;
  line-height: 35px;
  text-align: center;
  color: #3D4A56;
  margin-top: -5px;
  vertical-align: center;
}
.projects-container .projects .projects-table .projects-body .project .queries-count:hover {
  font-weight: 600;
  background: #E7F0F8;
  border-radius: 7px;
  color: #1975FF;
  cursor: pointer;
}
.projects-container .projects .projects-table .projects-body .project .top-10,
.projects-container .projects .projects-table .projects-body .project .top-20,
.projects-container .projects .projects-table .projects-body .project .top-30,
.projects-container .projects .projects-table .projects-body .project .top-50,
.projects-container .projects .projects-table .projects-body .project .top-100 {
  display: flex;
  flex-direction: row;
  gap: 5px;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
}
.projects-container .projects .projects-table .projects-body .project .top-10 .last-top,
.projects-container .projects .projects-table .projects-body .project .top-20 .last-top,
.projects-container .projects .projects-table .projects-body .project .top-30 .last-top,
.projects-container .projects .projects-table .projects-body .project .top-50 .last-top,
.projects-container .projects .projects-table .projects-body .project .top-100 .last-top {
  text-align: right;
  color: #3D4A56;
  width: 42px;
}
.projects-container .projects .projects-table .projects-body .project .top-10 .cur-top,
.projects-container .projects .projects-table .projects-body .project .top-20 .cur-top,
.projects-container .projects .projects-table .projects-body .project .top-30 .cur-top,
.projects-container .projects .projects-table .projects-body .project .top-50 .cur-top,
.projects-container .projects .projects-table .projects-body .project .top-100 .cur-top {
  text-align: left;
  width: 42px;
}
.projects-container .projects .projects-table .projects-body .project .top-10 .cur-top.positive,
.projects-container .projects .projects-table .projects-body .project .top-20 .cur-top.positive,
.projects-container .projects .projects-table .projects-body .project .top-30 .cur-top.positive,
.projects-container .projects .projects-table .projects-body .project .top-50 .cur-top.positive,
.projects-container .projects .projects-table .projects-body .project .top-100 .cur-top.positive {
  color: #0D941A;
}
.projects-container .projects .projects-table .projects-body .project .top-10 .cur-top.negative,
.projects-container .projects .projects-table .projects-body .project .top-20 .cur-top.negative,
.projects-container .projects .projects-table .projects-body .project .top-30 .cur-top.negative,
.projects-container .projects .projects-table .projects-body .project .top-50 .cur-top.negative,
.projects-container .projects .projects-table .projects-body .project .top-100 .cur-top.negative {
  color: #FE5555;
}
.projects-container .projects .projects-table .projects-body .project:hover {
  background: #F4F8FB;
}
.projects-container .projects .buttons {
  display: flex;
  flex-direction: column;
}
.projects-container .projects .buttons .button {
  margin-left: auto;
  margin-right: 20px;
  width: 50px;
  height: 50px;
  border-radius: 7px;
  border: none;
  outline: none;
  transition: width 0.5s;
  display: grid;
  grid-template-columns: 23px;
  grid-column-gap: 20px;
  justify-content: center;
  overflow: hidden;
}
.projects-container .projects .buttons .button .text {
  display: none;
}
.projects-container .projects .buttons .button:hover {
  grid-template-columns: 23px 155px;
  cursor: pointer;
  width: 195px;
}
.projects-container .projects .buttons .button:hover.download-program {
  width: 225px;
}
.projects-container .projects .buttons .button:hover .text {
  display: unset;
  font-size: 16px;
  height: 16px;
  line-height: 120%;
  text-align: left;
  color: #FFFFFF;
  margin: auto 0;
  width: 155px;
}
.projects-container .projects .buttons .button:hover.download-program .text {
  color: #1975FF;
}
.projects-container .projects .buttons .button:hover svg {
  margin-left: 14px;
  margin-right: 14px;
}
.projects-container .projects .buttons .button svg {
  width: 23px;
  height: 23px;
  margin: auto;
}
.projects-container .projects .buttons .button.add-site {
  margin-top: 20px;
  background: #1975FF;
}
.projects-container .projects .buttons .button.download-program {
  margin-top: 15px;
  background: #E7F0F8;
}
