body,
html {
  font-family: Manrope;
  font-weight: 600;
  font-style: normal;
  padding: 0;
  margin: 0;
}
html,
body,
#root,
.app {
  height: 100%;
  width: 100%;
  min-width: 1200px;
}
html {
  scrollbar-width: none;
}
html::-webkit-scrollbar {
  display: none;
}
input:focus::placeholder,
textarea:focus::placeholder {
  color: transparent !important;
}
button:not([disabled]) {
  cursor: pointer;
}
@media screen and (max-width: 479px) {
  html,
  body,
  #root,
  .app {
    min-width: unset;
  }
  .main-page {
    max-width: 100vw !important;
  }
  .main-page .main {
    padding-top: 30px !important;
    padding-bottom: 60px !important;
  }
  .main-page .main .container .logo {
    width: 340px;
    height: 232px;
    background-size: 340px 232px;
    margin: 10px auto 0;
  }
  .main-page .main .container .info {
    margin-top: 0;
  }
  .main-page .main .container .info .heading,
  .main-page .main .container .info .sub-heading {
    text-align: center;
  }
  .main-page .main .container .info .heading {
    font-size: 35px;
  }
  .main-page .main .container .info .sub-heading {
    margin-top: 10px;
    font-size: 14px;
  }
  .main-page .main .container .info .row {
    margin-top: 20px;
  }
  .main-page .main .container .info .register.button {
    width: 280px;
    margin: 0 auto;
  }
  .main-page .main .container .info .text {
    text-align: center;
    margin: 10px auto 0;
    font-size: 13px;
  }
  .main-page .main .row.beta-test {
    width: 340px !important;
    height: 225px !important;
    margin: 0 auto;
    position: relative;
  }
  .main-page .main .row.beta-test .lines-right,
  .main-page .main .row.beta-test .lines-left,
  .main-page .main .row.beta-test .arrow {
    display: none;
  }
  .main-page .main .row.beta-test .lines-mobile {
    display: block!important;
    position: absolute;
    top: 0;
    left: 0;
    mix-blend-mode: screen;
    background: transparent;
    border-radius: 15px;
    overflow: hidden;
  }
  .main-page .main .row.beta-test .arrow-mobile {
    display: unset!important;
    position: absolute;
    right: 25px;
    bottom: 112px;
  }
  .main-page .main .row.beta-test button {
    width: 290px !important;
    margin: 0 auto;
  }
  .main-page .main .row.beta-test .text {
    white-space: normal !important;
    margin: 30px 25px 20px 25px !important;
    width: 290px !important;
    height: 81px !important;
    text-align: center !important;
    font-size: 20px !important;
    line-height: 27px !important;
  }
  .main-page .main > .container .row {
    flex-direction: column;
  }
  .main-page .sub-main {
    padding: 40px 20px 48px 20px;
  }
  .main-page .sub-main .row:not(.item) {
    flex-direction: column;
  }
  .main-page .sub-main .item {
    margin-left: 0;
    margin-top: 30px !important;
  }
  .main-page .sub-main .text {
    font-size: 26px;
    width: 320px;
    margin: 0 auto;
    height: 102px;
  }
  .main-page .sub-main .text .big-star {
    bottom: 7px;
    right: 32px;
  }
  .main-page .sub-main .text .small-star {
    bottom: 31px;
    right: 0;
  }
  .main-page .how-works {
    padding: 60px 55px;
  }
  .main-page .how-works .heading {
    font-size: 26px;
  }
  .main-page .how-works .row {
    margin-top: 30px;
    flex-direction: column;
    gap: 30px;
  }
  .main-page .how-works .column {
    margin-top: 0 !important;
  }
  .main-page .benefits .container {
    padding: 49px 20px;
    gap: 40px;
  }
  .main-page .benefits .container .row {
    flex-direction: column;
  }
  .main-page .benefits .container .row > .icon {
    display: none;
  }
  .main-page .benefits .container .row .icon-mobile {
    width: 190px;
    height: 190px;
    background-size: 190px !important;
  }
  .main-page .benefits .container .row:nth-of-type(1) .icon-mobile {
    background: url(../svg/icon-benefits1.svg) no-repeat;
  }
  .main-page .benefits .container .row:nth-of-type(2) .icon-mobile {
    background: url(../svg/icon-benefits2.svg) no-repeat;
  }
  .main-page .benefits .container .row .column {
    gap: 20px;
  }
  .main-page .benefits .container .row .column .heading {
    font-size: 28px;
    line-height: 38px;
  }
  .main-page .benefits .container .row .column .list {
    margin-top: unset;
  }
  .main-page .interface {
    padding: 60px 10px;
  }
  .main-page .interface .container {
    margin: 0;
  }
  .main-page .interface .container .heading {
    font-size: 22px;
    line-height: 125%;
  }
  .main-page .interface .container .player .image {
    width: calc(100% - 12px);
    height: 224px;
    border-width: 6px;
    box-shadow: 0 6px 10px rgba(46, 57, 67, 0.1);
  }
  .main-page .interface .container .player .icon-play {
    width: 70px;
    height: 67.38px;
    background-size: 70px 67.38px;
  }
  .main-page .interface .container .player .mascot {
    width: 150px;
    height: 160px;
    right: 0;
    bottom: -36px;
    background-size: 150px 160px;
  }
  .main-page .add-in-month {
    padding: 50px 20px;
  }
  .main-page .add-in-month .container > .heading {
    font-size: 28px;
    line-height: 38px;
  }
  .main-page .add-in-month .row {
    flex-direction: column;
    gap: 33px;
  }
  .main-page .add-in-month .column {
    margin-top: 0 !important;
  }
  .main-page .add-in-month .column .heading {
    font-size: 20px;
    line-height: 27px;
    margin: 0 auto;
  }
  .main-page .tariff {
    padding: 60px 20px;
  }
  .main-page .tariff .heading {
    font-size: 28px;
    line-height: 38px;
  }
  .main-page .tariff .tariff-table {
    display: none;
  }
  .main-page .tariff .tariffs-mobile {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: center;
    margin-top: 20px;
  }
  .main-page .tariff .tariffs-mobile .tariff-item {
    background: #F4F8FB;
    border-radius: 7px;
    width: 120px;
    display: flex;
    flex-direction: column;
    padding: 15px 20px 19px;
  }
  .main-page .tariff .tariffs-mobile .tariff-item * {
    margin: 0 auto;
  }
  .main-page .tariff .tariffs-mobile .tariff-item .icon {
    width: 45px;
    height: 44px;
    background-repeat: no-repeat;
  }
  .main-page .tariff .tariffs-mobile .tariff-item:nth-of-type(1) .icon {
    background: url(../svg/tariff-zero.svg);
  }
  .main-page .tariff .tariffs-mobile .tariff-item:nth-of-type(2) .icon {
    background: url(../svg/tariff-junior.svg);
  }
  .main-page .tariff .tariffs-mobile .tariff-item:nth-of-type(3) .icon {
    background: url(../svg/tariff-middle.svg);
  }
  .main-page .tariff .tariffs-mobile .tariff-item:nth-of-type(4) .icon {
    background: url(../svg/tariff-senior.svg);
  }
  .main-page .tariff .tariffs-mobile .tariff-item:nth-of-type(5) .icon {
    background: url(../svg/tariff-mario.svg);
  }
  .main-page .tariff .tariffs-mobile .tariff-item:nth-of-type(6) .icon {
    background: url(../svg/tariff-prince.svg);
  }
  .main-page .tariff .tariffs-mobile .tariff-item:nth-of-type(7) .icon {
    background: url(../svg/tariff-boss.svg);
  }
  .main-page .tariff .tariffs-mobile .tariff-item .name {
    margin-top: 5px;
    font-size: 22px;
    line-height: 30px;
    text-align: center;
    color: #3D4A56;
  }
  .main-page .tariff .tariffs-mobile .tariff-item .min-expense,
  .main-page .tariff .tariffs-mobile .tariff-item .price {
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    color: #3D4A56;
  }
  .main-page .tariff .tariffs-mobile .tariff-item .heading {
    margin-top: 15px;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    text-align: center;
    color: #616872;
  }
  .main-page .tariff .tariffs-mobile .tariff-item .program-installed {
    margin-top: 10px;
    width: 35px;
    height: 33px;
    background: url(../svg/installed.svg) no-repeat;
  }
  .main-page .tariff .tariffs-mobile .tariff-item .program-installed.no {
    background-image: url(../svg/not-installed.svg);
  }
  .main-page .tariff .tariffs-mobile .tariff-item .min-expense {
    margin-top: 5px;
  }
  .main-page .tariff .tariffs-mobile .tariff-item .price {
    margin-top: 5px;
    font-weight: 600;
  }
  .main-page .tariff .button.add-site {
    margin: 30px auto 0;
  }
  .main-page > div > .container {
    max-width: unset;
  }
  .main-page .lines {
    display: none!important;
  }
  .player-modal .player {
    box-shadow: 0 6px 10px rgba(46, 57, 67, 0.1);
    width: calc(95%);
    padding: 6px;
  }
  .player-modal .player iframe {
    width: 100%;
    height: 224px;
    border-radius: 20px;
    margin: auto;
  }
  .footer {
    padding: 40px 0;
    flex-direction: column;
    justify-content: unset;
    gap: 20px;
  }
  .footer > div {
    margin: 0 auto !important;
  }
  .footer .text,
  .footer .links {
    text-align: center;
  }
  .footer .payments {
    order: 1;
    width: 340px;
    justify-content: center;
  }
  .footer .payments .tinkoff {
    order: 0;
  }
  .footer .payments .ur-pay {
    order: 1;
  }
  .footer .payments .apple-pay {
    order: 2;
  }
  .footer .payments .google-pay {
    order: 7;
  }
  .footer .payments .sber-pay {
    order: 8;
  }
  .footer .payments .qiwi {
    order: 9;
  }
  .footer .payments .mastercard {
    order: 3;
  }
  .footer .payments .visa {
    order: 4;
  }
  .footer .payments .mir {
    order: 5;
  }
  .footer .payments .sbp {
    order: 6;
  }
  .footer .payments .yoo-money {
    order: 10;
  }
  .footer .payments .paddle {
    order: 11;
  }
  .header-container {
    height: 60px;
  }
  .header-container .header {
    width: 100%;
    padding: 0 20px 0 10px;
  }
  .header-container .header .heading .logo {
    background: url(../svg/logo.svg) no-repeat;
    width: 51px;
    height: 43px;
    background-size: 51px 43px;
  }
  .header-container .header .heading div {
    margin-left: 7.06px;
  }
  .header-container .header .heading div .span:nth-of-type(1) {
    font-size: 20.1689px;
    line-height: 28px;
  }
  .header-container .header .heading div .span:nth-of-type(2) {
    font-size: 11.7652px;
    line-height: 16px;
  }
  .header-container .header .middle,
  .header-container .header .right {
    display: none!important;
  }
  .header-container .header .mobile-open-menu {
    display: unset!important;
  }
  .header-container .header .mobile-open-menu,
  .header-container .header .mobile-open-menu svg {
    margin: auto 0;
  }
  .menu-mobile {
    position: fixed;
    top: 60px;
    left: 0;
    width: 100%;
    background: #F4F8FB;
    height: 100%;
    z-index: 100;
  }
  .menu-mobile .menu-main {
    border-top: 1px solid #E7F0F8;
    border-bottom: 1px solid #E7F0F8;
    padding: 30px 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  .menu-mobile .menu-main .heading {
    font-weight: 400;
    font-size: 17px;
    line-height: 23px;
    color: #34495E;
  }
  .menu-mobile .menu-main .buttons {
    display: flex;
    flex-direction: row;
    gap: 13px;
  }
  .menu-mobile .menu-main .buttons button {
    display: flex;
    flex-direction: row;
    gap: 7px;
    justify-content: center;
    border-radius: 7px;
    border: none;
  }
  .menu-mobile .menu-main .buttons button .text {
    font-weight: 500;
    font-size: 17px;
    line-height: 17px;
    height: 17px;
    color: #FFFFFF;
    margin: auto 0;
  }
  .menu-mobile .menu-main .buttons button .icon {
    width: 25px;
    height: 25px;
    margin: auto 0;
  }
  .menu-mobile .menu-main .buttons .register {
    width: 187px;
    height: 55px;
    background: #1975FF;
  }
  .menu-mobile .menu-main .buttons .login {
    width: 120px;
    height: 55px;
    background: #FFA319;
  }
  .menu-mobile .menu-main .buttons .login .icon {
    background: url(../svg/login-icon-mobile.svg) no-repeat;
  }
  .menu-mobile .menu-main .buttons .register .icon {
    background: url(../svg/register-icon-mobile.svg) no-repeat;
  }
  .menu-mobile .links {
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding: 30px 20px;
  }
  .menu-mobile .links a {
    font-weight: 400;
    font-size: 17px;
    line-height: 23px;
    color: #34495E;
    text-decoration: none;
  }
  .menu-mobile + .header-container {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
  }
}
