.footer {
  background: #F4F8FB;
  width: 100%;
  padding: 60px 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.footer .copyright {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.footer .copyright .logo-container {
  display: flex;
  flex-direction: row;
  gap: 8px;
}
.footer .copyright .logo-container .logo {
  background: url(../svg/logo.svg);
  width: 53px;
  height: 45px;
}
.footer .copyright .logo-container .header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 55px;
}
.footer .copyright .heading {
  font-size: 24px;
  line-height: 24px;
  color: #1975FF;
}
.footer .copyright .sub-heading {
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  color: #3D4A56;
}
.footer .copyright .text {
  font-weight: 400;
  font-size: 15px;
  line-height: 145%;
  color: #616872;
  width: 180px;
}
.footer .payments {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 6.5px;
  width: 590px;
  margin: auto 0 auto 90px;
}
.footer .payments div {
  width: 78.98px;
  height: 41.81px;
}
.footer .payments div:hover {
  cursor: pointer;
}
.footer .payments .tinkoff,
.footer .payments .ur-pay,
.footer .payments .yoo-money,
.footer .payments .paddle {
  width: 120.79px;
  height: 41.81px;
}
.footer .payments .tinkoff {
  background: url(../svg/tinkoff-icon.svg) no-repeat;
}
.footer .payments .ur-pay {
  background: url(../svg/ur-pay-icon.svg) no-repeat;
}
.footer .payments .apple-pay {
  background: url(../svg/apple-pay-icon.svg) no-repeat;
}
.footer .payments .google-pay {
  background: url(../svg/google-pay-icon.svg) no-repeat;
}
.footer .payments .sber-pay {
  background: url(../svg/sber-pay-icon.svg) no-repeat;
}
.footer .payments .qiwi {
  background: url(../svg/qiwi-icon.svg) no-repeat;
}
.footer .payments .mastercard {
  background: url(../svg/mastercard-icon.svg) no-repeat;
}
.footer .payments .visa {
  background: url(../svg/visa-icon.svg) no-repeat;
}
.footer .payments .mir {
  background: url(../svg/mir-icon.svg) no-repeat;
}
.footer .payments .sbp {
  background: url(../svg/sbp-icon.svg) no-repeat;
}
.footer .payments .yoo-money {
  background: url(../svg/yoo-money-icon.svg) no-repeat;
}
.footer .payments .paddle {
  background: url(../svg/paddle-icon.svg) no-repeat;
}
.footer .links {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin: auto 0 auto 70px;
}
.footer .links a {
  font-weight: 400;
  font-size: 15px;
  line-height: 145%;
  color: #616872;
  text-decoration: none;
}
